@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  width: 100%;
  height: 100%;
}

body {
  color: #feffff;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
}

a {
  color: #feffff;
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

h1 {
  font-size: 2em;
  font-weight: 400;
}

h2 {
  font-size: 1.5em;
  font-weight: 400;
}

.header {
  width: 100%;
  height: 5em;
  background: #2b7a78;
}

.header__container {
  max-width: 1200px;
  height: 100%;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.header__container-logo {
  width: 10em;
  margin: .5em;
}

.header__container-nav {
  height: 100%;
}

.header__container-nav a {
  height: 100%;
  width: 10em;
  text-align: center;
  font-size: 20px;
  font-weight: 300;
  line-height: 4em;
  display: inline-block;
}

.active {
  background: #17252a;
}

.documentation {
  background: #fff;
  overflow: hidden;
}

.documentation__sidebar {
  width: 300px;
  float: left;
  padding: 2em 2em 0;
  position: absolute;
}

.documentation__sidebar-menu {
  width: 100%;
  border-bottom: 1px solid #17252a;
  margin-bottom: 2em;
  padding-bottom: 1em;
}

.documentation__sidebar-menu a {
  width: 100%;
  color: #17252a;
  padding: 20px 0;
  display: inline-block;
}

.documentation__content {
  min-height: 600px;
  width: calc(100% - 300px);
  background: #f7f7fa;
  margin-left: 300px;
  padding: 2em 75px 50px 3em;
  font-weight: 300;
  line-height: 1.5em;
  overflow: hidden;
}

.documentation__content-title {
  color: #17252a;
  padding: .25em 0;
  font-weight: 600;
}

.documentation__content p {
  color: #17252a;
  padding: .5em 0;
}

.documentation__content-installing {
  color: #17252a;
  padding: .25em 0;
  line-height: 1em;
}

.documentation__content a {
  color: #17252a;
  text-decoration: underline;
}

.documentation__content-installing-code {
  background: #def2f1;
  border-radius: .75em;
  line-height: 1em;
}

pre {
  flex-direction: column;
  justify-content: start;
  display: flex;
}

pre code.hljs {
  background: #def2f1;
  border-radius: .75em;
  padding: 2em;
}

.footer {
  width: 100%;
  height: 7em;
  background: #2b7a78;
  font-size: 1.2em;
  display: flex;
}

.footer__container {
  margin: auto;
}

.footer__container-nametag span {
  opacity: .75;
}

